const spanWords = (words, classNameAdd) =>
  words.map((word) =>
    word.startsWith("http") ? (
      <a target="_blank" href={word}>
        <span className={`word ${classNameAdd}`}>
          {word}
          <Nbsp />
        </span>
      </a>
    ) : (
      <span className={`word ${classNameAdd}`}>
        {word}
        <Nbsp />
      </span>
    )
  );

const Card = (card) => {
  const word = (field) =>
    card[field] && <>{spanWords(card[field].split(" "), field)}</>;

  const list = (title, items) =>
    (items || []).length > 0 && (
      <>
        <span className="word listMeta">
          <b>
            {`${title}:`}
            <Nbsp />
          </b>
        </span>
        {spanWords((items || []).join(", ").split(" "), "listItem")}
      </>
    );
  return (
    <div className="card leftBorder">
      <p className="wrapping wrappingCenter">
        {word("title")}
        {word("institution")}
        {word("year")}
        {word("description")}
        {config.show.topics && list("topics", card.topics)}
        {config.show.cmdf && list("cmd+f", card.cmdf)}
      </p>
      <style jsx global>
        {`
          .card {
            width: 600px;
            max-width: 90vw;
          }
          .card .title {
            font-size: 18px;
            font-weight: bold;
          }

          .card .institution {
            font-weight: bold;
            color: ${gray(4)};
          }

          a .description {
            // color: ${gray(5)};
            // text-decoration-color: ${gray(6)};
          }

          .card .year {
            color: ${gray(5)};
            font-weight: normal;
            font-size: 14px;
          }

          .listMeta {
            color: ${gray(4)};
          }
          .listItem {
            color: ${gray(5)};
          }
        `}
      </style>
    </div>
  );
};

const Section = ({ title, copy, cards }) => (
  <section id={`section-${title}`}>
    <h2>{`${title}: `}</h2>
    {copy && (
      <div className="copy">
        <p>{spanWords(copy.split(" "), "sectionCopy")}</p>
      </div>
    )}
    <div className="wrapping wrappingBaseline">
      {cards.map((card) => (
        <div className="datum">
          <Card {...card} />
        </div>
      ))}
    </div>
    <style jsx>
      {`
        .copy {
          font-size: 12px;
          color: ${gray(3)};
        }
      `}
    </style>
  </section>
);

const Other = () => {
  return (
    <section id={`section-other`}>
      <span id="otherTitle">{`${config.other.title}: `}</span>
      {_(config.other.items)
        .flatMap(([item, href]) => [
          <span className="">{`, `}</span>,
          <a href={href}>
            <span className="">{item}</span>
          </a>,
        ])
        .tail()
        .value()}
      <style jsx>{`
        #otherTitle {
          font-size: 20px;
          font-weight: bold;
        }
      `}</style>
    </section>
  );
};

